@import 'variables';

@font-face {
  font-family: '#{$icomoon-font-family}';
  src:
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?hpmv5u') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?hpmv5u') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?hpmv5u##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^='icon-'], [class*=' icon-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-success {
  &:before {
    content: '\e908';
  }
}
.icon-error_outline {
  &:before {
    content: '\e907';
  }
}
.icon-select-arrows {
  &:before {
    content: '\e905'
  }
}
.icon-arrow-right {
  &:before {
    content: '\e902';
  }
}
.icon-checkmark {
  &:before {
    content: '\e904';
  }
}
.icon-lock-open {
  &:before {
    content: '\e903';
  }
}
.icon-lock {
  &:before {
    content: '\e901';
  }
}
.icon-phone {
  &:before {
    content: '\e900';
  }
}